<template>
<div class="flex p-2">
    <input :placeholder="placeHolder"
    :value="inputValue" 
    @input="$emit('update:inputValue',$event.target.value)"
    class="p-2 border border-gray-300 rounded-lg overflow-hidden focus:outline-none"/>
</div>
</template>
<script>
export default {
    props:{
        placeHolder:{
            type:String,
            required:true,
            default:'enter input'
        },
        inputValue:{
            type:String,
            default:'',
            required:true
        }
    },
    emits:['update:inputValue']
}
</script>