import { createApp } from 'vue'
import App from './App.vue'
import router from '@/router'
import './assets/styles.css'
import BaseInputSearch from './components/core/BaseInputSearch.vue'
import BaseDropDown from './components/core/BaseDropDown.vue'
import BaseTable from './components/core/BaseTable.vue'
import BaseInput from './components/core/BaseInput.vue'
import BaseButton from './components/core/BaseButton.vue'
import Default from '@/layouts/Default'
import Admin from '@/layouts/Admin'
import 'material-icons/iconfont/material-icons.css';
import axiosPlugin from '@/plugins/axios.plugin'
import veeValidatePlugin from '@/plugins/vee-validate.plugin'
const app = createApp(App)
app.use(axiosPlugin)
app.use(veeValidatePlugin)
app.use(router)
app.component('BaseInputSearch',BaseInputSearch)
app.component('BaseDropDown',BaseDropDown)
app.component('BaseTable',BaseTable)
app.component('BaseInput',BaseInput)
app.component('BaseButton',BaseButton)
app.component('default-layout',Default)
app.component('admin-layout',Admin)
app.mount('#app')


